html, body {
  background-color: #FBF7ED;
  color: #004C3F;
  height: 100%;
  margin: 0;
  overflow: hidden;
}

h1, h4 {
  padding-left: 20px;
}

input[type=text] {
  width: 90%;
  height: 35px;
  text-indent: 30px;
  color: #212326;
  font-size: 1em;
  box-shadow: 0 0 0 1px #6b7177;
  border: 1px solid transparent;
  border-radius: 4px;
  background-color: #ffffff;
  transition: padding 150ms;
  -webkit-appearance: none;
  appearance: none;
}

span {
  display: inline;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #FBF7ED; 
}
 
::-webkit-scrollbar-thumb {
  background: #004C3F; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #008060; 
}

.noms-header {
  background-color: #004C3F;
  color: #FBF7ED;
  padding: 5px 5px 5px 0px;
}

.search-bar {
  position: relative;
  width: 100%;
  text-align:center;
}

.search-bar span {
  position: absolute;
  left: 2px;
  margin-left: 20px;
  padding-top: 7px;
}

.wrapper {
  position: relative;
}

.wrapper span {
  position: absolute;
  left: 2px;
}

.search-result {
  width: 90%;
  height: 35px;
  margin: 20px 20px 0px 20px;
  color: #212326;
  box-shadow: 0 0 0 1px #6b7177;
  border: 1px solid transparent;
  border-radius: 4px;
  background-color: #ffffff;
  transition: padding 150ms;
  -webkit-appearance: none;
  appearance: none;
  display: flex;
}

.search-result span {
  position: inherit;
  margin-right: 10px;
  margin-top: 5px;
}

.movie-title {
  color: #004C3F;
  margin-top: 5px;
  margin-left: 5px;
  width: 85%;
  height: 100%;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}

.movie-year {
  color: #004C3F;
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 10px;
  white-space: nowrap;
}

.nominations-year {
  color: #FBF7ED;
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 40px;
}

.nominations-list {
  width: 90%;
  height: 35px;
  margin: 0px 20px 10px 20px;
  border: 1px solid transparent;
  border-radius: 4px;
  transition: padding 150ms;
  -webkit-appearance: none;
  appearance: none;
  display: flex;
}

.nominations-list p {
  width: 80%;
  position: absolute;
  margin-left: 35px;
  margin-top: 2px;
  white-space: initial;
  overflow-x: hidden;
  text-overflow: ellipsis;
}

.search-results-container {
  overflow-y: scroll;
  position: relative;
  height: calc(100vh - 200px);
}

.nominate-button {
  color: #FBF7ED;
  background-color: #008060;
  width: 125px;
  margin: 3px;
  border: none;
  border-radius: 5px;
  display: inline-flex;
  align-items: center;
}

.nominate-button:hover {
  cursor: pointer;
  background-color: #004C3F;
}

.nominate-button:disabled {
  cursor: default;
  background-color: #008060;
}

.remove-button:hover {
  cursor: pointer;
}

.shoppies-title {
  color: black;
  font-style: italic;
  font-weight: 900;
  font-family: 'Roboto', sans-serif;
  display: flex;
  left: -40px;
}

.shoppies-title-text {
  margin-top: 26px;
  margin-left: -5px;
}

.shoppies-subtitle-text {
  margin-top: -10px;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
}
.shopify-logo {
  width: 34px;
  height: 39px;
  background-repeat: no-repeat;
  background-image: url("shopify_logo.svg");
  margin-left: 20px;
  margin-top: 24px;
}

/* Snackbar https://www.w3schools.com/howto/howto_js_snackbar.asp */
#noms-done-snackbar {
  display: flex;
  visibility: hidden;
  min-width: 250px; 
  margin-left: -125px;
  background-color: #004C3F;
  color: #FBF7ED;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  font-weight: bold;
}

#noms-done-snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

/* Loader indicator https://www.w3schools.com/howto/tryit.asp?filename=tryhow_css_loader5 */
#loader {
  position: absolute;
  left: 50%;
  top: 20%;
  z-index: 1;
  width: 50px;
  height: 50px;
  margin: -76px 0 0 -76px;
  border: 8px solid #008060;
  border-radius: 50%;
  border-top: 8px solid #004C3F;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.animate-bottom {
  position: relative;
  -webkit-animation-name: animatebottom;
  -webkit-animation-duration: 1s;
  animation-name: animatebottom;
  animation-duration: 1s
}

@-webkit-keyframes animatebottom {
  from { bottom:-100px; opacity:0 } 
  to { bottom:0px; opacity:1 }
}

@keyframes animatebottom { 
  from{ bottom:-100px; opacity:0 } 
  to{ bottom:0; opacity:1 }
}

.fade-in-animation {
  animation: fadeInAnimation 1s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}

